import React from 'react';
import { Task, TaskState } from '../types';

function KanbanBoard() {
  const [tasks, setTasks] = React.useState<Task[]>([]);

  React.useEffect(() => {
    const fetchTasks = async () => {
      const response = await fetch('/api')
      if (!response.ok) {
        console.error('Failed to fetch tasks:', response.statusText);
        return;
      }
      const data = await response.json()
      setTasks(data);
      console.log(data)
    };
    fetchTasks();
  }, []);
// filter the tasks to there columns 
  const todoTasks = tasks.filter(task => task.state === TaskState.ToDo);
  const inProgressTasks = tasks.filter(task => task.state === TaskState.InProgress);
  const completedTasks = tasks.filter(task => task.state === TaskState.Completed);

  return (
    <div className="p-4">
      <div className="flex gap-4">
        {/* To Do Column */}
        <div className="bg-gray-100 p-4 rounded-lg w-80">
          <h2 className="font-bold mb-4 text-black">To Do</h2>
          <div className="min-h-[500px]">
            {todoTasks.map(task => (
              <div key={task.id} className="bg-white p-4 mb-2 rounded shadow">
                {task.text}
              </div>
            ))}
          </div>
        </div>

        {/* In Progress Column */}
        <div className="bg-gray-100 p-4 rounded-lg w-80">
          <h2 className="font-bold mb-4 text-black">In Progress</h2>
          <div className="min-h-[500px]">
            {inProgressTasks.map(task => (
              <div key={task.id} className="bg-white p-4 mb-2 rounded shadow">
                {task.text}
              </div>
            ))}
          </div>
        </div>

        {/* Completed Column */}
        <div className="bg-gray-100 p-4 rounded-lg w-80">
          <h2 className="font-bold mb-4 text-black">Completed</h2>
          <div className="min-h-[500px]">
            {completedTasks.map(task => (
              <div key={task.id} className="bg-white p-4 mb-2 rounded shadow">
                {task.text}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default KanbanBoard;